body {
  font-family: 'Lato', sans-serif; }

.container {
  margin: 0 auto;
  max-width: 1000px;
  padding: 0 10px; }

ul {
  padding: 0;
  margin: 0; }

.header_first_block {
  background-color: #011432;
  color: #fff; }

.header_data {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-top: 7px;
  padding-bottom: 9px; }
  .header_data__social_link {
    margin: 0;
    padding: 0;
    list-style-type: none; }
    .header_data__social_link li {
      display: inline-block;
      margin-right: 12px; }
  .header_data__tel {
    font-size: 14px;
    font-weight: 100;
    margin-left: 30px;
    word-spacing: 5px; }

.header_menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 9px; }
  .header_menu__logo {
    color: #002258;
    padding: 30px 100px;
    border: 1px solid #18234b;
    font-size: 30px;
    font-weight: 100; }
  .header_menu_list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
    max-width: 650px;
    width: 70%; }
    .header_menu_list__link {
      padding: 20px 0;
      position: relative; }
      .header_menu_list__link a {
        color: #002258;
        font-size: 13px;
        font-weight: 400;
        text-decoration: none;
        text-transform: uppercase; }
      .header_menu_list__link:hover .header_menu_list__link-dropdown {
        display: block; }
      .header_menu_list__link-dropdown {
        display: none;
        position: absolute;
        left: -21px;
        margin-top: 13px;
        padding-bottom: 30px;
        z-index: 1111;
        list-style-type: none;
        background-color: #fff;
        border-top: 4px solid #0070e9; }
        .header_menu_list__link-dropdown_link:hover .header_menu_list__link-dropdown_link__subdropdown {
          display: block; }
        .header_menu_list__link-dropdown_link:hover .header_menu_list__link-dropdown_link-a {
          color: #0070ed;
          background-color: #fbfbfb; }
        .header_menu_list__link-dropdown_link__subdropdown {
          display: none;
          padding-right: 30px;
          position: absolute;
          left: calc(100% + 1px);
          height: 100%;
          background: #fff;
          top: -4px;
          list-style: none;
          border-top: 4px solid #0070e9; }
          .header_menu_list__link-dropdown_link__subdropdown-link {
            width: 100%; }
            .header_menu_list__link-dropdown_link__subdropdown-link a {
              padding: 12px 10px 12px 21px;
              display: block;
              font-family: Corbel;
              text-transform: none;
              font-size: 14px;
              white-space: nowrap; }
              .header_menu_list__link-dropdown_link__subdropdown-link a:hover {
                color: #0070ed;
                background-color: #fbfbfb; }
        .header_menu_list__link-dropdown_link a {
          display: block;
          padding: 12px 51px 12px 21px;
          font-family: Corbel;
          text-transform: none;
          font-size: 14px; }
          .header_menu_list__link-dropdown_link a:hover {
            color: #0070ed;
            background-color: #fbfbfb; }
        .header_menu_list__link-dropdown::before, .header_menu_list__link-dropdown::after {
          content: "";
          position: absolute;
          left: 21px;
          top: -14px;
          border: 5px solid transparent;
          border-bottom: 5px solid #0070e9; }

a.header_menu_list__link-dropdown_link__subdropdown-link-a {
  padding-right: 0; }

#burger_menu {
  display: none; }

#burger_menu {
  cursor: pointer; }
  #burger_menu span {
    display: block;
    background: #002258;
    height: 5px;
    margin: 4px 0;
    width: 34px;
    border-radius: 8px; }

#close {
  position: absolute;
  right: 32px;
  top: 32px;
  width: 32px;
  height: 32px;
  opacity: 1;
  cursor: pointer; }
  #close::before, #close::after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: #002258; }
  #close::before {
    transform: rotate(45deg); }
  #close::after {
    transform: rotate(-45deg); }

.mobile_menu {
  background-color: black;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: -100%;
  z-index: 9999;
  transition-duration: 300ms; }
  .mobile_menu_list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    list-style-type: none;
    background-color: #fff;
    border-top: 4px solid #002258; }
    .mobile_menu_list__link a {
      color: #002258;
      font-size: 15px;
      text-transform: uppercase;
      text-decoration: none;
      margin: 15px 0;
      display: block; }
    .mobile_menu_list__link::after {
      content: '';
      border-bottom: 1px solid #ccc;
      width: 33px;
      margin: 0 auto;
      display: block; }
    .mobile_menu_list__link:last-child::after {
      display: none; }

.slider_item {
  position: relative; }
  .slider_item img {
    width: 100%; }
  .slider_item_textholder {
    position: absolute;
    top: calc(50% - 110px);
    width: 100%;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.7);
    font-weight: 300;
    padding-left: 75px;
    padding-top: 40px;
    padding-bottom: 16px; }
    .slider_item_textholder__title {
      font-size: 51.51px; }
    .slider_item_textholder__subtitle {
      font-size: 42.93px; }
    .slider_item_textholder__text {
      margin-top: 10px;
      font-size: 13px;
      font-weight: 400; }

.products__title {
  margin: 40px 0 12px 0;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
  color: #002258; }

.products_list {
  display: flex;
  justify-content: space-between; }
  .products_list__item {
    width: calc(20% - 6px);
    position: relative;
    max-height: 193px; }
    .products_list__item img {
      width: 100%;
      height: 100%;
      filter: brightness(50%); }
    .products_list__item-text {
      position: absolute;
      bottom: 0px;
      font-weight: 300;
      font-size: 12px;
      color: #fff;
      text-transform: uppercase;
      text-align: center;
      width: 100%;
      padding: 20px 0;
      background-color: rgba(0, 0, 0, 0.7); }
    .products_list__item:hover div.products_list__item-text {
      padding: 0;
      top: 0;
      padding-top: calc(50% - 0.5em); }
    .products_list__item:nth-child(3) .products_list__item-text {
      padding: 14px 0; }

.products__underline {
  margin: 17px auto;
  border-bottom: 1px solid #bfbfbf;
  max-width: 600px; }

.about {
  margin-bottom: 40px; }
  .about_container {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .about_container_item {
      position: relative;
      text-decoration: none;
      display: block;
      width: calc(50% - 5px); }
      .about_container_item__text {
        position: absolute;
        top: calc(50% - 0.5em);
        width: 100%;
        text-align: center;
        color: #fff;
        text-transform: uppercase;
        font-weight: 300;
        font-size: 40px;
        text-align: center; }
      .about_container_item img {
        width: 100%;
        filter: brightness(50%);
        height: 244px;
        object-fit: cover; }
      .about_container_item:hover {
        transform: scale(1.02);
        transition-duration: 300ms; }

.footer {
  background-color: #011432;
  color: #fff;
  padding-top: 20px;
  padding-bottom: 25px; }
  .footer_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .footer_wrapper__copyright {
      font-weight: 100;
      font-size: 14px; }
      .footer_wrapper__copyright img {
        margin-right: 10px; }
    .footer_wrapper__menu {
      font-weight: 300;
      font-size: 14px;
      text-transform: uppercase;
      margin: 0;
      padding: 0; }
      .footer_wrapper__menu li {
        display: inline-block;
        padding: 0 15px;
        border-right: 1px solid #fff; }
        .footer_wrapper__menu li a {
          color: #fff;
          text-decoration: none; }
        .footer_wrapper__menu li:last-child {
          border: 0;
          padding-right: 20px; }

.slick-dots li button:before {
  content: '◯';
  opacity: 1;
  font-size: 12px;
  color: white; }

.slick-dots li.slick-active button:before {
  color: white;
  opacity: 1;
  content: '•';
  font-size: 15px;
  padding-top: 2px; }

.slick-dots {
  bottom: 15px; }

@media screen and (max-width: 950px) {
  .header_menu__logo {
    padding: 30px 38px; } }

@media screen and (max-width: 796px) {
  .products_list {
    flex-wrap: wrap;
    justify-content: flex-start; }
    .products_list__item {
      margin: 5px;
      width: calc(50% - 10px); }
      .products_list__item img {
        object-fit: cover; }
      .products_list__item:hover div.products_list__item-text {
        padding-top: calc(27.6% - .5em); }
  .about_container_item {
    margin: 5px; } }

@media screen and (max-width: 768px) {
  .products_list__item:hover div.products_list__item-text {
    top: inherit;
    padding: 20px 0; }
  .products_list__item:nth-child(3):hover .products_list__item-text {
    padding: 14px 0; }
  .about_container_item:hover {
    transform: none; }
  #burger_menu {
    display: block;
    margin: 0; }
  .header_menu {
    flex-direction: row-reverse; }
    .header_menu_list {
      display: none; }
    .header_menu__logo {
      padding: 18px 36px; } }

@media screen and (max-width: 720px) {
  .slider_item_textholder {
    padding-left: 35px; }
    .slider_item_textholder__text {
      padding-right: 42px; } }

@media screen and (max-width: 680px) {
  .about_container {
    flex-direction: column; }
    .about_container_item {
      width: calc(100% - 10px);
      margin: 5px; } }

@media screen and (max-width: 573px) {
  .footer_wrapper__menu {
    display: none; }
  .slider_item_textholder__text {
    display: none; }
  .slick-dots li button:before {
    font-size: 8px; }
  .slick-dots li.slick-active button:before {
    font-size: 11px; } }

@media screen and (max-width: 500px) {
  .slider_item_textholder {
    top: calc(50% - 3em);
    padding-top: 16px; }
    .slider_item_textholder__title {
      font-size: 25px; }
    .slider_item_textholder__subtitle {
      font-size: 20px; } }

@media screen and (max-width: 440px) {
  .about_container_item img {
    height: auto; } }

@media screen and (max-width: 320px) {
  .products_list__item {
    width: calc(100% - 5px); } }
